import axios from "axios";
import React, { useEffect } from "react";

const MurActivaton = ({
  boolMur,
  setBoolMur,
  boolProgramme,
  setBoolProgramme,
}) => {
  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}api/Mur/${2}`)
      .then((response) => {
        setBoolProgramme(response.data.bool);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  useEffect(() => {
    const element = document.querySelector(".activer");
    const p = element.querySelector("p");
    if (boolMur === "true") {
      p.style.backgroundColor = "green";
    } else {
      p.style.backgroundColor = "red";
    }
  }, [boolMur]);

  useEffect(() => {
    const element = document.querySelector(".activerProgramme");
    const p = element.querySelector("p");
    if (boolProgramme === "true") {
      p.style.backgroundColor = "green";
    } else {
      p.style.backgroundColor = "red";
    }
  }, [boolProgramme]);

  function Axios(id, bool) {
    const data = {
      bool: bool,
    };
    axios
      .put(`${process.env.REACT_APP_API_URL}api/Mur/${id}`, data)
      .then((response) => {
        console.log("modifiÃ©", response);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <div className="muractivation">
      <h2>Activer le mur d'expressions</h2>
      <div className="activer">
        {boolMur === "true" ? (
          <p
            className="pointer"
            onClick={() => {
              Axios(1, "false");
              setBoolMur("false");
            }}
          >
            ON
          </p>
        ) : (
          <p
            className="pointer"
            onClick={() => {
              Axios(1, "true");
              setBoolMur("true");
            }}
          >
            OFF
          </p>
        )}
      </div>
      <h2>Rendre disponible le programme</h2>
      <div className="activerProgramme">
        {boolProgramme === "true" ? (
          <p
            className="pointer"
            onClick={() => {
              Axios(2, "false");
              setBoolProgramme("false");
            }}
          >
            ON
          </p>
        ) : (
          <p
            className="pointer"
            onClick={() => {
              Axios(2, "true");
              setBoolProgramme("true");
            }}
          >
            OFF
          </p>
        )}
      </div>
    </div>
  );
};

export default MurActivaton;
