import React, { useEffect } from "react";
import Navigation from "../components/commun/Navigation";
import Liens from "../components/liensUtile/Liens";
import Footer from "../components/commun/Footer";

const LiensUtiles = ({ widthScreen, scrollPage, heightScreen, boolMur }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <React.Fragment>
      <Navigation
        bol_nosfonds={false}
        bol_transparent={false}
        widthScreen={widthScreen}
        scrollPage={scrollPage}
        boolMur={boolMur}
      />
      <Liens widthScreen={widthScreen} heightScreen={heightScreen} />
      {/* <Footer /> */}
    </React.Fragment>
  );
};

export default LiensUtiles;
